import styled from "styled-components";

export const RightMenuSC = styled.div`
  height: 100%;
  width: 500px;
  padding: 55px 20px 0 20px;
  background: #2e2e2e;
  border-radius: 30px 0 0 30px;
  position: absolute;
  right: 0;
  top: 0;
  overflow: auto;
  transition: .4s ease-in-out;
`;